import React from 'react'
import Buttons from './Buttons'
//libreria para validar propiedades por tipo, es decir String,INT,Bool, etc.
import PropTypes from 'prop-types'
import "./Contenedor.css"

const Contenedor = ({title,url,contenido}) => {
    return (
        <div className="container-content" >
            <div >
                <img className='card' src={url} />
            </div>
            <div className="content-card">
                {title}<br></br>
                <div className="contenido">
                    {contenido}
                </div>
            </div>
            <div className="btn-content">
                <Buttons text="Ver más" color="azul"/>
                <Buttons text="Agregar" color="verde" />
            </div>
        </div>

    )
}
//metodo de validación por Props
Contenedor.propTypes ={
    title : PropTypes.string,
    url : PropTypes.string,
    contenido : PropTypes.string
}



export default Contenedor