import React from "react";
import Noticia from "./Noticia";
import Contenedor from "./Contenedor";
import "./Noticias.css"

const Noticias = () =>{
    const data = [
        {
            "logo" : "img/icono-soltoluca.jpg",
            "titulo" : "Sol de Toluca",
            "texto" : "La aerolínea suspendió operaciones hace un año cuando la pandemia agravó sus finanzas, que ya estaban afectadas.",
            "url":"img/avion.jpg"
        },
        {
            "logo" : "img/icono-financiero.png",
            "titulo" : "EL FINANCIERO",
            "texto" : "Esta temporada, los mexicanos podremos gozar de posadas, cenas... ¿y Covid-19? Si no quieres arruinar tus planes, sigue estas recomendaciones.",
            "url" : "img/posada.jfif"
        },
        {
            "logo" : "img/icono-php.jpg",
            "titulo" : "DESARROLLADORES WEB",
            "texto" : "Mañana voy a hacer código en directo por YouTube, recién estoy empezando en mundo del desarrollo web y quisiera que pudiesen ver para que luego me apoyen con críticas constructivas. Es un reto para mi hacer esto en frente de muchas personas de otros países. Soy de Perú....Voy a hacer un CRUD con PHP. ",
            "url" : "img/php.jfif"
        },
        {
            "logo" : "img/icono-tutprog.jpg",
            "titulo" : "Tutoriales de programación",
            "texto" : "Como puedes centrar en CSS. Espero les sea de ayuda!.",
            "url" : "img/css.png"
        },
        {
            "logo" : "img/icono-react.jpg",
            "titulo" : "React Js Español Latino",
            "texto" : "Hola, estoy usando react con typescript y tengo este error, alguien sabe por que no me toma la imagen? No se encuentra el módulo  ni sus declaraciones de tipos correspondientes.ts(2307) qué debería hacer?",
            "url" : "img/react.jpg"
        }
    ]
    const dataGaleria = [
        {
            "title" : "Metallica",
            "url" : "img/metallica.jpg",
            "contenido" : "Banda fundada en E.U.A con más de 20 años"
        },
        {
            "title" : "Disturbed",
            "url" : "img/disturbed.jpg",
            "contenido" : "Banda fundada en E.U.A con más de 15 años,con exitos como Prayer, Decadence, etc.."
        },
        {
            "title" : "System of A Down",
            "url" : "img/system.jpg",
            "contenido" : "Banda fundada en E.U.A con más de 12 años, con más de 7 discos vendidos."
        },
        {
            "title" : "Slayer",
            "url" : "img/slayer.png",
            "contenido" : "Banda fundada en E.U.A con más de 30 años, considerada la mejor del mundo."
        },
        {
            "title" : "Slipknot",
            "url" : "img/slipknot.png",
            "contenido" : "Banda fundada en E.U.A con más de 12 años, su genéro es el NU-Metal, mescla de rap y metal10"
        },
        {
            "title" : "Lamb of God",
            "url" : "img/lambofgod.jpg",
            "contenido" : "Banda fundada en E.U.A con más de 12 años, su genéro es el Grove Metal y considerara de las mejores."
        },
        {
            "title" : "Guns of Roses",
            "url" : "img/guns.png",
            "contenido" : "Banda fundada en E.U.A con más de 15 años, banda de tiempos memoriales como Nirvana de los años 90."
        }
    ]
    return (
        <>
            <div className="content-galeria">
                {
                    dataGaleria.map(({title,url,contenido}) => {
                        return <Contenedor key={title} title={title} url={url} contenido={contenido}/>
                    })
                }
            </div>
            <div className="contenedor">
                {
                    data.map(({logo,titulo,texto,url}) =>{
                        return <Noticia  logo={logo} titulo={titulo} texto={texto} url={url}/>
                    })
                }
            </div>            
        </>


    )
}

export default Noticias