import React from "react";
import PropTypes from 'prop-types'
import "./Noticia.css"

const Noticia = ({logo,titulo,texto,url}) =>{
    return(
        <div className="noticia">
            <div className="logo">
                <img src={logo} className="img-logo" />
            </div>
            <div className="titulo">
                <h4>
                    {titulo}
                </h4>
            </div>
            <div className="texto-articulo">
                {texto}
            </div>
            <div className="img-articulo">
                <img className="img-local" src={url}  />
                <hr style={{width:"90%"}}></hr>
            </div>
            <div className="buttons">
                <button className="btn">Me gusta</button>
                <button className="btn">Comentar</button>
                <button className="btn">Compartir</button>
            </div>   
        </div>
    )
}

Noticia.propTypes = {
    logo : PropTypes.string.isRequired,
    titulo : PropTypes.string,
    texto : PropTypes.string,
    url : PropTypes.string
}


export default Noticia